import Categories from "../components/Categories";
import { Inner } from "../components/Page";
import styled from "styled-components";

const Hero = styled(AlreadyStyledComponent)`
  &&& {
    max-width: 100vw;
    img {
      height: 30vh;
    }
  }
`;

const Home = props => (
  <div>
    <Hero className={props.className}>
      <img
        src="https://res.cloudinary.com/da91pbpmj/image/upload/v1544389906/jason-leung-479251-unsplash.jpg"
        alt="Search bar and header"
        srcset=""
      />
    </Hero>
    <Categories />
  </div>
);

export default Home;
